/* Table Styles */

.table-wrapper{
    margin: 10px 3% 3%;
    box-shadow: 0px 35px 50px rgba( 0, 0, 0, 0.2 );
}

.fl-table {
    border-radius: 5px;
    font-size: 12px;
    font-weight: normal;
    border: none;
    border-collapse: collapse;
    width: 100%;
    white-space: nowrap;
    background-color: white;
    table-layout:fixed;
}

.fl-table td, .fl-table th {
    text-align: center;
    padding: 8px;
    overflow: hidden;
    width: auto;
}

table tr th:nth-child(3){
    width: 60%;
}

.fl-table td {
    border-right: 1px solid #f8f8f8;
    font-size: 16px;
    cursor: pointer;
}

.fl-table thead th {
    color: #ffffff;
    background: var(--psyellow);
}


.fl-table thead th:nth-child(odd) {
    color: #ffffff;
    background: var(--psblue);
}

.fl-table tr:nth-child(even) {
    background: #F8F8F8;
}
.fl-table tr:hover{
    background: #F2F2F2;
}

#formSearchbox {
    margin: 0 3%;
    width: 94%;
    font-size: 24px;
    cursor: text;
}

